<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary"
                     @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="accountList"
                border
                stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label=""
                         prop="code"></el-table-column>
        <el-table-column label=""
                         prop="name"></el-table-column>
        <el-table-column label=""
                         prop="currentBalance"></el-table-column>
        <el-table-column label=""
                         prop="beginBalance"></el-table-column>
        <el-table-column label=""
                         prop="balanceTime"
                         :formatter="dateFormatter"></el-table-column>
        <el-table-column label=""
                         prop="typeName"></el-table-column>
        <el-table-column label=""
                         width="120px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark"
                        content=""
                        placement="top"
                        :enterable="false">
              <el-button type="primary"
                         icon="el-icon-edit"
                         size="mini"
                         @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark"
                        content=""
                        placement="top"
                        :enterable="false">
              <el-button type="danger"
                         icon="el-icon-delete"
                         size="mini"
                         @click="deleteAccount(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title=""
               :visible.sync="addDialogVisible"
               width="50%"
               @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm"
               :rules="addFormRules"
               ref="addFormRef"
               label-width="90px">
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="code">
              <el-input v-model="addForm.code"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""
                          prop="name">
              <el-input v-model="addForm.name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="balanceTime">
              <el-date-picker v-model="addForm.balanceTime"
                              type="date"
                              value-format='yyyy-MM-dd'
                              placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""
                          prop="beginBalance">
              <el-input v-model="addForm.beginBalance"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="type">
              <el-select v-model="addForm.type"
                         placeholder="">
                <el-option v-for="type in typeList"
                           :key="type.id"
                           :label="type.name"
                           :value="type.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="addAccount"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title=""
               :visible.sync="editDialogVisible"
               width="50%"
               @close="editDialogClosed">
      <!--  -->
      <el-form :model="editForm"
               :rules="editFormRules"
               ref="editFormRef"
               label-width="90px">
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="code">
              <el-input v-model="editForm.code"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""
                          prop="name">
              <el-input v-model="editForm.name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="balanceTime">
              <el-date-picker v-model="editForm.balanceTime"
                              type="date"
                              value-format='yyyy-MM-dd'
                              placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=""
                          prop="beginBalance">
              <el-input v-model="editForm.beginBalance"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label=""
                          prop="type">
              <el-select v-model="editForm.type"
                         placeholder="">
                <el-option v-for="type in typeList"
                           :key="type.id"
                           :label="type.name"
                           :value="type.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editAccount"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      params: {
        query: {
        }
      },
      typeList: [],
      accountList: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        beginBalance: 0,
        balanceTime: new Date()
      },
      // 
      addFormRules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getTypeList()
    this.getAccountList()
  },
  methods: {
    async getTypeList () {
      const { data: result } = await this.$http.post('/dict/itemList', {
        dictCode: 'account_type'
      })
      if (!result.success) return this.$message.error(result.message)

      this.typeList = result.data.itemList
    },
    async getAccountList () {
      const { data: result } = await this.$http.post('/settlementAccount/list', this.params)
      if (!result.success) return this.$message.error(result.message)

      this.accountList = result.data.accountList
      this.accountList.forEach((account) => {
        account.currentBalance = (account.currentBalance / 100.0).toFixed(2)
        account.beginBalance = (account.beginBalance / 100.0).toFixed(2)
      })
    },
    // 
    dateFormatter (row, column) {
      var date = row[column.property]
      if (date === undefined) { return '' }
      return this.$moment(date).format('YYYY-MM-DD')
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addAccount () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        var beginBalance = parseInt(parseFloat(this.addForm.beginBalance) * 100)
        if (isNaN(beginBalance)) {
          this.$message.error('')
          return
        }
        const { data: result } = await this.$http.post('/settlementAccount/save', {
          account: {
            code: this.addForm.code,
            name: this.addForm.name,
            balanceTime: this.addForm.balanceTime,
            type: this.addForm.type,
            beginBalance: beginBalance
          }
        })
        if (!result.success) {
          return this.$message.error(result.message)
        }

        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getAccountList()
      })
    },
    // 
    async showEditDialog (accountId) {
      const { data: result } = await this.$http.post('/settlementAccount/detail', {
        accountId
      })

      if (!result.success) return this.$message.error(result.message)

      this.editForm = result.data.account
      this.editForm.beginBalance = (this.editForm.beginBalance / 100.0).toFixed(2)
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async editAccount () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return

        var beginBalance = parseInt(parseFloat(this.editForm.beginBalance) * 100)
        if (isNaN(beginBalance)) {
          this.$message.error('')
          return
        }

        // 
        const { data: result } = await this.$http.post('/settlementAccount/save', {
          account: {
            id: this.editForm.id,
            code: this.editForm.code,
            name: this.editForm.name,
            balanceTime: this.editForm.balanceTime,
            type: this.editForm.type,
            beginBalance: beginBalance
          }
        })

        if (!result.success) {
          return this.$message.error(result.message)
        }

        // 
        this.editDialogVisible = false
        // 
        this.getAccountList()
        // 
        this.$message.success('')
      })
    },
    // 
    async deleteAccount (accountId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: result } = await this.$http.post('/settlementAccount/delete', { accountId })

      if (!result.success) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getAccountList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-date-editor {
  width: 100%;
}
.el-select {
  width: 100%;
}
</style>
