<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary"
                     @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="employeeList"
                border
                stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label=""
                         prop="code"></el-table-column>
        <el-table-column label=""
                         prop="name"></el-table-column>
        <el-table-column label=""
                         width="70px">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.active"
                       @change="employeeStateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label=""
                         width="120px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark"
                        content=""
                        placement="top"
                        :enterable="false">
              <el-button type="primary"
                         icon="el-icon-edit"
                         size="mini"
                         @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark"
                        content=""
                        placement="top"
                        :enterable="false">
              <el-button type="danger"
                         icon="el-icon-delete"
                         size="mini"
                         @click="deleteEmployee(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="params.current"
                     :page-sizes="[1, 2, 5, 10]"
                     :page-size="params.size"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title=""
               :visible.sync="addDialogVisible"
               width="30%"
               @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm"
               :rules="addFormRules"
               ref="addFormRef"
               label-width="80px">
        <el-form-item label=""
                      prop="code">
          <el-input v-model="addForm.code"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="addEmployee"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title=""
               :visible.sync="editDialogVisible"
               width="30%"
               @close="editDialogClosed">
      <!--  -->
      <el-form :model="editForm"
               :rules="editFormRules"
               ref="editFormRef"
               label-width="80px">
        <el-form-item label=""
                      prop="code">
          <el-input v-model="editForm.code"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editEmployee"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      params: {
        query: {
        },
        // 
        current: 1,
        // 
        size: 5
      },
      total: 0,
      employeeList: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
      },
      // 
      addFormRules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getEmployeePage()
  },
  methods: {
    async getEmployeePage () {
      const { data: result } = await this.$http.post('/employee/page', this.params)
      if (!result.success) return this.$message.error(result.message)

      this.employeeList = result.data.employeePage.records
      this.total = result.data.employeePage.total
    },
    //  pagesize 
    handleSizeChange (size) {
      this.params.size = size
      this.getEmployeePage()
    },
    //   
    handleCurrentChange (current) {
      this.params.current = current
      this.getEmployeePage()
    },
    async employeeStateChanged (employee) {
      const { data: result } = await this.$http.post(
        '/employee/switchActive',
        {
          'employeeId': employee.id
        }
      )
      if (!result.success) {
        employee.active = !employee.active
        return this.$message.error('')
      }
      employee.active = result.data.employee.active
      this.$message.success('')
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addEmployee () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: result } = await this.$http.post('/employee/save', {
          employee: this.addForm
        })
        if (!result.success) {
          return this.$message.error(result.message)
        }

        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getEmployeePage()
      })
    },
    // 
    async showEditDialog (employeeId) {
      const { data: result } = await this.$http.post('/employee/detail', {
        employeeId
      })

      if (!result.success) return this.$message.error(result.message)

      this.editForm = result.data.employee
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async editEmployee () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return

        // 
        const { data: result } = await this.$http.post('/employee/save', {
          employee: this.editForm
        })

        if (!result.success) {
          return this.$message.error(result.message)
        }

        // 
        this.editDialogVisible = false
        // 
        this.getEmployeePage()
        // 
        this.$message.success('')
      })
    },
    // 
    async deleteEmployee (employeeId) {
      // 
      const confirmResult = await this.$confirm(
        ' ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: result } = await this.$http.post('/employee/delete', { employeeId })

      if (!result.success) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getEmployeePage()
    }
  }
}
</script>

<style lang="less" scoped>
</style>
