<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-container>
      <el-aside width="200px">
        <el-card>
          <el-tree :data="categoryList"
                   :props="defaultProps"
                   default-expand-all
                   node-key="id"
                   ref="tree"
                   highlight-current
                   :expand-on-click-node="false"
                   @node-click="handleNodeClick"></el-tree>
        </el-card>
      </el-aside>
      <el-main>
        <!--  -->
        <el-card>
          <!--  -->
          <el-row :gutter="20"
                  class="query">
            <el-col :span="8">
              <el-input placeholder="//"
                        v-model="params.query.name"
                        clearable
                        @clear="getProductPage">
              </el-input>
            </el-col>
            <el-col :span="1.5">
              <el-button type="primary"
                         @click="search"></el-button>
            </el-col>
            <el-col :span="1.5">
              <el-button type="success"
                         @click="clear"></el-button>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="1.5">
              <el-button type="primary"
                         @click="showAddDialog()"></el-button>
            </el-col>
          </el-row>

          <!--  -->
          <el-table :data="productList"
                    border
                    stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label=""
                             prop="categoryName"></el-table-column>
            <el-table-column label=""
                             prop="code"></el-table-column>
            <el-table-column label=""
                             prop="name"></el-table-column>
            <el-table-column label=""
                             prop="spec"></el-table-column>
            <el-table-column label=""
                             prop="unitName"></el-table-column>
            <el-table-column label=""
                             prop="stock"></el-table-column>
            <el-table-column label=""
                             prop="beginQuantity"></el-table-column>
            <el-table-column label=""
                             prop="unitCost"></el-table-column>
            <el-table-column label=""
                             prop="beginTotalAmount"></el-table-column>
            <el-table-column label=""
                             prop="estimatedPurchasePrice"></el-table-column>
            <el-table-column label=""
                             prop="retailPrice"></el-table-column>
            <el-table-column label=""
                             prop="remark"></el-table-column>
            <el-table-column label=""
                             width="70px">
              <template slot-scope="scope">
                <el-switch v-model="scope.row.active"
                           @change="productStateChanged(scope.row)">
                </el-switch>
              </template>
            </el-table-column>
            <el-table-column label=""
                             width="120px">
              <template slot-scope="scope">
                <!--  -->
                <el-tooltip effect="dark"
                            content=""
                            placement="top"
                            :enterable="false">
                  <el-button type="primary"
                             icon="el-icon-edit"
                             size="mini"
                             @click="showEditDialog(scope.row.id)"></el-button>
                </el-tooltip>
                <!--  -->
                <el-tooltip effect="dark"
                            content=""
                            placement="top"
                            :enterable="false">
                  <el-button type="danger"
                             icon="el-icon-delete"
                             size="mini"
                             @click="deleteProduct(scope.row.id)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>

          <!--  -->
          <el-pagination @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :current-page="params.current"
                         :page-sizes="[1, 2, 5, 10]"
                         :page-size="params.size"
                         layout="total, sizes, prev, pager, next, jumper"
                         :total="total">
          </el-pagination>
        </el-card>
      </el-main>
    </el-container>

    <!--  -->
    <el-dialog :title="isAdd ? '' : ''"
               :visible.sync="saveDialogVisible"
               width="70%"
               @close="saveDialogClosed">
      <el-form :model="saveForm"
               :rules="saveFormRules"
               ref="saveFormRef"
               label-width="100px">
        <el-tabs type="border-card">
          <el-tab-pane label="">
            <!--  -->
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="code">
                  <el-input v-model="saveForm.code"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="name">
                  <el-input v-model="saveForm.name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="barcode">
                  <el-input v-model="saveForm.barcode"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="spec">
                  <el-input v-model="saveForm.spec"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="categoryId">
                  <el-select :value="saveForm.categoryName"
                             placeholder=""
                             clearable
                             @clear="handleCategorySelectClear"
                             ref="selectCategory">
                    <el-option :label="saveForm.categoryName"
                               :value="saveForm.categoryId"
                               style="width: 100%;height:200px;overflow: auto;background-color:#fff">
                      <el-tree :data="categoryList[0].childList"
                               :props="defaultProps"
                               :expand-on-click-node=false
                               @node-click="handleCategoryNodeClick"></el-tree>
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="primaryWarehouseId">
                  <el-select v-model="saveForm.primaryWarehouseId"
                             placeholder="()"
                             clearable>
                    <el-option v-for="warehouse in warehouseList"
                               :key="warehouse.id"
                               :label="warehouse.name"
                               :value="warehouse.id">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="unitId">
                  <el-select v-model="saveForm.unitId"
                             placeholder="()"
                             clearable>
                    <el-option v-for="unit in unitList"
                               :key="unit.id"
                               :label="unit.name"
                               :value="unit.id">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-divider content-position="left"></el-divider>
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="retailPrice">
                  <el-input v-model="saveForm.retailPrice"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="wholesalePrice">
                  <el-input v-model="saveForm.wholesalePrice"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="VIP"
                              prop="vipPrice">
                  <el-input v-model="saveForm.vipPrice"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="discountRate1">
                  <el-input v-model="saveForm.discountRate1">
                    <template slot="append">%</template>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="discountRate2">
                  <el-input v-model="saveForm.discountRate2">
                    <template slot="append">%</template>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="estimatedPurchasePrice">
                  <el-input v-model="saveForm.estimatedPurchasePrice"></el-input>
                </el-form-item>
              </el-col>
              <el-col>
                <el-form-item label=""
                              prop="remark">
                  <el-input type="textarea"
                            :rows="2"
                            placeholder=""
                            v-model="saveForm.remark">
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-tab-pane>
          <el-tab-pane label="">
            <el-row>
              <el-col :span="8">
                <el-form-item label=""
                              prop="minimumStock">
                  <el-input v-model="saveForm.minimumStock"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=""
                              prop="maximumStock">
                  <el-input v-model="saveForm.maximumStock"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="saveDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="saveProduct"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="isContactAdd ? '' : ''"
               :visible.sync="saveContactDialogVisible"
               width="70%"
               @close="saveContactDialogClosed">
      <!--  -->
      <el-form :model="saveContactForm"
               :rules="saveContactFormRules"
               ref="saveContactFormRef"
               label-width="100px">
        <el-row>
          <el-col :span="8">
            <el-form-item label=""
                          prop="name">
              <el-input v-model="saveContactForm.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=""
                          prop="mobile">
              <el-input v-model="saveContactForm.mobile"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=""
                          prop="phone">
              <el-input v-model="saveContactForm.phone"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="QQ/"
                          prop="qq">
              <el-input v-model="saveContactForm.qq"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=""
                          prop="address">
              <el-input v-model="saveContactForm.address"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label=""
                          prop="primary">
              <el-switch v-model="saveContactForm.primary">
              </el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="saveContactDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="saveContact"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      defaultProps: {
        children: 'childList',
        id: 'id',
        label: 'name'
      },
      // 
      params: {
        query: {
          categoryId: ''
        },
        // 
        current: 1,
        // 
        size: 5
      },
      total: 0,
      categoryList: [
        {
          id: '',
          name: ''
        }
      ],
      warehouseList: [],
      unitList: [],
      levelList: [],
      productList: [],

      isAdd: true,
      // 
      saveDialogVisible: false,
      // 
      saveForm: {
        categoryId: null,
        vatRate: 17,
        contactList: []
      },

      // 
      saveFormRules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      isContactAdd: true,
      modifyContactIndex: 0,
      saveContactDialogVisible: false,
      saveContactForm: {
      },
      // 
      saveContactFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    'saveForm.beginReceivableAmountDisplay' (newVal, oldVal) {
      if (newVal === undefined || newVal === null || newVal === '') {
        this.saveForm.beginReceivableAmount = 0
        return
      }
      this.saveForm.beginReceivableAmount = parseInt(parseFloat(newVal) * 100)
      if (isNaN(this.saveForm.beginReceivableAmount)) {
        this.$message.error('')
      }
    },
    'saveForm.beginPrepaidAmountDisplay' (newVal, oldVal) {
      if (newVal === undefined || newVal === null || newVal === '') {
        this.saveForm.beginPrepaidAmount = 0
        return
      }
      this.saveForm.beginPrepaidAmount = parseInt(parseFloat(newVal) * 100)
      if (isNaN(this.saveForm.beginPrepaidAmount)) {
        this.$message.error('')
      }
    }
  },
  created () {
    this.getCategoryList()
    this.getWarehouseList()
    this.getUnitList()

    this.getProductPage()
  },
  methods: {
    // 
    handleNodeClick (data) {
      this.params.query.categoryId = data.id
      this.getProductPage()
    },
    // 
    async getCategoryList () {
      const { data: result } = await this.$http.post('/category/list', {
        type: 30
      })
      if (!result.success) return this.$message.error(result.message)

      this.categoryList = [
        {
          id: '',
          name: '',
          childList: result.data.categoryList
        }
      ]
    },
    // 
    async getWarehouseList () {
      const { data: result } = await this.$http.post('/warehouse/page', {
        current: 1,
        size: 10000
      })
      if (!result.success) return this.$message.error(result.message)

      this.warehouseList = result.data.warehousePage.records
    },
    // 
    async getUnitList () {
      const { data: result } = await this.$http.post('/dict/itemList', {
        dictCode: 'unit'
      })
      if (!result.success) return this.$message.error(result.message)

      this.unitList = result.data.itemList
    },
    // 
    search () {
      this.getProductPage()
    },
    // 
    clear () {
      this.params.query = {}
      this.$refs.tree.setCurrentNode({
        id: ''
      })
      this.getProductPage()
    },
    // 
    async getProductPage () {
      const { data: result } = await this.$http.post('/product/page', this.params)
      if (!result.success) return this.$message.error(result.message)

      this.productList = result.data.productPage.records

      this.total = result.data.productPage.total
    },
    //  pagesize 
    handleSizeChange (size) {
      this.params.size = size
      this.getProductPage()
    },
    //   
    handleCurrentChange (current) {
      this.params.current = current
      this.getProductPage()
    },
    // 
    async productStateChanged (product) {
      const { data: result } = await this.$http.post(
        '/product/switchActive',
        {
          'productId': product.id
        }
      )
      if (!result.success) {
        product.active = !product.active
        return this.$message.error('')
      }
      product.active = result.data.product.active
      this.$message.success('')
    },
    // 
    handleCategoryNodeClick (node) {
      this.saveForm.categoryId = node.id
      this.saveForm.categoryName = node.name
      this.$refs.selectCategory.blur()
    },
    // 
    handleCategorySelectClear () {
      this.saveForm.categoryId = null
      this.saveForm.categoryName = ''
    },

    // 
    showAddDialog () {
      this.isAdd = true
      this.saveDialogVisible = true
    },
    // 
    async showEditDialog (productId) {
      const { data: result } = await this.$http.post('/product/detail', {
        productId
      })
      if (!result.success) return this.$message.error(result.message)

      this.saveForm = result.data.product

      console.log(this.saveForm)

      this.isAdd = false
      this.saveDialogVisible = true
    },
    // 
    saveDialogClosed () {
      this.$refs.saveFormRef.resetFields()
      this.saveForm = {
        categoryId: null,
        vatRate: 17,
        contactList: []
      }
    },
    // 
    async saveProduct () {
      this.$refs.saveFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: result } = await this.$http.post('/product/save', {
          product: this.saveForm
        })
        if (!result.success) {
          return this.$message.error(result.message)
        }

        this.$message.success('')
        // 
        this.saveDialogVisible = false
        // 
        this.getProductPage()
      })
    },
    // 
    async deleteProduct (productId) {
      // 
      const confirmResult = await this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: result } = await this.$http.post('/product/delete', { productId })

      if (!result.success) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getProductPage()
    },

    // 
    showContactAddDialog () {
      this.isContactAdd = true
      this.saveContactForm = {}
      this.saveContactDialogVisible = true
    },
    // 
    showContactEditDialog (contact, index) {
      this.isContactAdd = false
      this.saveContactForm = this.$_.cloneDeep(contact)
      this.modifyContactIndex = index
      this.saveContactDialogVisible = true
    },

    // 
    saveContactDialogClosed () {
      if (this.isContactAdd) {
        this.$refs.saveContactFormRef.resetFields()
      }
    },
    // 
    saveContact () {
      this.$refs.saveContactFormRef.validate(async valid => {
        if (!valid) return

        var contact = this.$_.cloneDeep(this.saveContactForm)

        // 
        if (this.saveForm.contactList == null || this.saveForm.contactList.length === 0) {
          contact.primary = true
        } else if (contact.primary) {
          this.saveForm.contactList.forEach((existedContact) => {
            existedContact.primary = false
          })
        }

        // 
        if (!this.isContactAdd) {
          this.saveForm.contactList.splice(this.modifyContactIndex, 1, contact)

          this.$message.success('')
        } else {
          // 
          this.saveForm.contactList.push(contact)
          this.$message.success('')
        }

        // 
        this.saveContactDialogVisible = false
      })
    },
    // 
    async contactPrimaryChanged (contact) {
      if (contact.primary) {
        this.saveForm.contactList.forEach((existedContact) => {
          existedContact.primary = false
        })

        contact.primary = true
      }

      this.$message.success('')
    },
    // 
    async deleteContact (contact) {
      // 
      const confirmResult = await this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      this.saveForm.contactList.splice(
        this.saveForm.contactList.findIndex(existedContact => existedContact === contact),
        1
      )

      this.$message.success('')
    }
  }
}
</script>

<style lang="less" scoped>
.query {
  margin-bottom: 20px;
}
.el-select {
  width: 100%;
}
.el-date-editor {
  width: 100%;
}
.el-main {
  padding-top: 0;
}
</style>
